@import "reset.css";

* {
    margin: 0px;
    padding: 0px; 
    box-sizing: border-box;
}

#pond {
    // top: 50%; 
    // left: 50%;
    max-width: 100%;
    max-height: 100%;
    display: none;
}

// styling for background

body {
    background-color: rgb(217, 198, 249);
    cursor: url('http://127.0.0.1:5500/pictures/cursor.png') 120 120, auto;
}


#start-button {
    width: 60%;
    height: 54px;
    font-size: 25px;
    background: #e3bed0;
    color: white;
    border-radius: 8px;
    display: flex;
    margin-top: 80px;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  
#start-button:hover {
    background: #eb96d3;
}

.small.game-title {
    font-size: 64px;
    margin-bottom: 80px;
}

#introduction-content {
    z-index: 999;
    position: fixed;
    width: 60vw;
    top: 5vh;
    left: 20vw;
    margin-top: 200px;
    padding-top: 100px;
    padding-left: 200px;
    padding-right: 200px;
    padding-bottom: 100px;
    background: white;
    box-shadow: 12px 12px 12px rgba(86, 50, 85, 0.25);
    border-radius: 20px;
    font-size: 18px;
    line-height: 28px;
    color: #3E3E3E;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

  @media only screen and (max-width: 823px) {
    #introduction-content {
      position: fixed;
      width: 94vw;
      top: 5vh;
      left: 3vw;
      padding: 20px;
      box-shadow: 8px 8px 8px rgba(240, 235, 235, 0.25);
      border-radius: 15px;
      font-size: 13px;
      line-height: 18px;
    }

  }

#game-description {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    font-size: 30px;
}

.hidden {
    display: none, 
}

.pull {
    z-index: 1000;
    // left: 100px;
    // top: 100px;
    position: absolute;
    height: 100px;
    width: 100px;
    background-color: white;
    border-radius: 50%;
    display: inline-block // might need to change
}

#pull_warning {
    z-index:1000;
    visibility:hidden;
    position:absolute;
    left: 1100px;
    top: 500px;
    font: sans-serif;
    color: white;
}

// #score {
//     justify-content: center;
// }

#score_box {
    width: 10%;
    height: 100px;
    top:200px;
    left:2000px;
    font-size:30px;
    font-family: sans-serif;
    vertical-align: text-top;
    background: transparent;
    color:#eaf3f2;
    border-radius: 8px;
    display: flex;
    margin-top: 80px;
    justify-content: right;
    align-items: right;
    position: absolute;

    //pacman shape
    width: 20px;
    height: 20px;
    border-right: 80px solid transparent;
    border-top: 60px solid rgb(232, 151, 214);
    border-left: 140px solid rgb(232, 151, 214);
    border-bottom: 70px solid rgb(232, 151, 214);
    border-top-left-radius: 80px;
    border-top-right-radius: 80px;
    border-bottom-left-radius: 80px;
    border-bottom-right-radius: 80px;
    
    
}

#strikes {
    width: 10%;
    height: 100px;
    top:200px;
    left:100px;
    font-size:30px;
    // background: white;
    color: black;
    border-radius: 8px;
    display: flex;
    margin-top: 80px;
    justify-content: center;
    align-items: center;
    position: absolute;
    }

#fish {
    width: 100%;
    height: 1000px;
    top:250px;
    left:0px;
    color: black;
    border-radius: 8px;
    display: flex;
    z-index: 998;
    position: absolute;
    }

#background {
    width: 100%;
    height: 80%;
    left:0px;
    top:250px;
    position: absolute;
    z-index: -1;
}

html, body, h1, h2 {
    margin: 0;
    padding: 0;
    border: 0;
    font: sans-serif;
    text-align: center;
}

// for fishie border 

h1 {
    font-size: 60px;
    margin: 30px;
    color: rgb(54, 170, 139);
    text-overflow: h1; 
    white-space: nowrap;
    overflow: hidden;
}


// github and linked in icon links 
